</script>

<style scoped lang="scss">
.NotFound{
  @include wh(100%,100%);
  background-color: white;
  // background:rgba(0,0,0,0.8);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999999999999999999999999999999999999999999999999999999999;
